

import XHeader from 'vux/src/components/x-header/index.vue'

import head_bg from '../../assets/head_bg.jpg'
export default{
components : {
XHeader,
},
data() {
return {
headBg : "background:url('"+ head_bg +"') center center no-repeat",
}
},
methods : {
headshake (ev){

ev.target.classList.add('active');

setTimeout(function (){
ev.target.classList.remove('active');
},750);

},
}
}




